.register label {
    display: block;
    position: relative;
    margin: 40px 0px;
    color: #333333;
    font-weight: 600;
}

.register .label-txt {
    position: absolute;
    top: -1.6em;
    padding: 10px;
    font-family: sans-serif;
    font-size: .8em;
    letter-spacing: 1px;
    color: rgb(120,120,120);
    transition: ease .3s;
}

.register button {
    display: inline-block;
    padding: 12px 24px;
    background: rgb(220,220,220);
    font-weight: bold;
    color: rgb(120,120,120);
    border: none;
    outline: none;
    border-radius: 3px;
    cursor: pointer;
    transition: ease .3s;
}

.register button:hover {
    background: #222222;
    color: #ffffff;
}
.error-page {
    -webkit-user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
         user-select: none;
    font-family: 'Montserrat', sans-serif;
    height: 90vh;
}

.error-page .error-code {
    z-index: -1;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 224px;
    font-weight: 900;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: -12px;
    color: #030005;
    text-transform: uppercase;
    text-shadow: -1px -1px 0px #8400ff, 1px 1px 0px #ff005a;
    letter-spacing: -20px;
}

.error-page .error-text {
    font-size: xx-large;
    font-weight: 700;
    text-transform: uppercase;
    text-shadow: 0px 2px 0px #8400ff;
    letter-spacing: 13px;
    margin: 0;
}

.error-page .error-page-button {
    display: inline-block;
    text-transform: uppercase;
    color: #ff005a;
    text-decoration: none;
    border: 2px solid;
    background: transparent;
    padding: 10px 40px;
    font-size: 14px;
    font-weight: 700;
    transition: 0.2s all;
}

.error-page .error-page-button:hover {
    color: #8400ff;
}

@media only screen and (max-width: 767px) {
    .error-page .error-text h2 {
        font-size: 24px;
    }
}

@media only screen and (max-width: 480px) {
  .error-page .error-code {
      font-size: 182px;
  }
}
.pop-up-box {
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    z-index: 3;
    background: rgba(177, 177, 177, 0.479);
    padding: 10px;
    vertical-align: middle;
}

.boards-loader .loader-view {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.boards .card .card-content .card-title {
    padding-left: 10px;
    font-family: "Open Sans Condensed", sans-serif;
    font-size: x-large;
    color: #333333;
}

.boards .card .card-img-wrapper .card-img {
    max-height: 180px;
}

.boards .card .board-options {
    position: absolute;
    bottom: 40%;
    left: 0;
    right: 0;
    margin-right: auto;
    margin-left: auto;
    opacity: 0;
}

.boards .card .card-img-wrapper:hover .board-options {
    opacity: 1;
}

.boards .card .animate {
    transition: all 0.2s ease-in-out;
}

.boards .card .btn {
    font-family: "Open Sans Condensed", sans-serif;
    font-size: x-large;
    font-weight: bold;
    color: rgb(255, 255, 255);
    text-shadow: 0 0 3px #FF0000, 0 0 5px #dddddd;
}

.boards .card .btn:hover {
    color: rgb(211, 146, 146);
}

.noboards-text {
    color: white;
    font-size: x-large;
    font-weight: 700;
    text-shadow: 1px 1px 1px #af3f45;
    letter-spacing: 2px;
    margin: 0;
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10+ and Edge */
    -moz-user-select: none;
         user-select: none; /* Standard syntax */
}

.create-board-window {
    z-index: 1;
    position: absolute;
    width: 300px;
    border-radius: 10px;
    padding-top: 50px;
    padding-bottom: 50px;
    vertical-align: middle;
    transition: 0.5s all;
    
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.moveable-component {
    position: absolute !important; 
}
.cards-container {
    margin-top: 1px;
    overflow-y: scroll;
}

.cards-container .card {
    padding: 8px 8px 2px !important;
    position: relative;
    border-radius: 3px !important;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -ms-word-break: break-all;
    word-break: break-word;
